.timeline {
    padding: 0 !important;
}

/*header*/

/* Header title*/
.timeline .timeline_header {
    padding-top: 7px;
}

.timeline .timeline_firstItem {
    min-height: 80px !important;
}

.timeline .MuiTimelineItem-root{
    min-height: 0px;
}

.timeline .MuiTimelineItem-root:before{
    display: none;
}


.timeline .timeline_dot_header{
    color: black;
    background-color: var(--main-color);
    font-size: small;
    padding: 10px;
}

/*remaining items*/

.timeline .timeline_dot{
    color: black;
    border-color: var(--main-color);
    padding: 2px;
}

.timeline .MuiTimelineConnector-root{
    background-color: var(--main-color);
}

.timeline .timeline_dot_header > .MuiSvgIcon-root{
    font-size: 20px;
    color: darkslategray;
}

.timeline .seperator_padding {
    padding-left: 18px;
}
.timeline .timeline_content {
    padding-top: 0px;
    margin-bottom: 20px;
}